@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0
}

.App {
  text-align: center;
  min-height: 100%;
  position: relative;
  padding-bottom: 60px;
}
a {
  text-decoration: none;
}



.Router {
  text-align: center;
  min-height: calc(100vh - 75px);
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


.MainNav {
  display: -webkit-flex;
  display: flex;
  background-color: #3E78B2;
  font-family: 'Oswald', sans-serif;
}

.MainNav .logoImg {
  height: 50px;
  width: auto;
  margin: 20px 5px 5px 15px;
}

.MainNav h1 {
  color: #E3E6F2;
}

.MainNav--list {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 5px;
  list-style: none;
  margin-top: 40px;
}

.MainNav--list--item a{
  margin-top: 20px;
  color: #E3E6F2;
  margin-right: 30px;

}

/* Home page */
.DashboardContainer {
  text-align: center;
}

.DashboardContainer form {
  margin-top: 10px;
}

.dataSelectors {
  background-color: #E3E6F2;
  width: 80%;
  margin: 0 auto;
  padding: 10px;
border-radius: 10%;
}


.studentList,
.ExerciseList {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.studentList li {
  padding: 10px;
}

.studentList li a {
  color: black;
}

.ExerciseList li {
  padding: 10px;
  background-color: #3E78B2;
  height: 100px;
  width: 100px;
  border-radius: 50%;
margin-top: 15px;
}

.ExerciseList li a {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100px;
  width: 100px;
  color: black;
}

/* About page */


.aboutText {
  width: 60%;
  margin: 0 auto;
}

/* Student page */

.Student {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-left: 50px;
}

.studentNameImg,
.studentContactInfo {
  width: 50%;
}


.studentContactInfo {
  margin-top: 90px;
  background-color: #E3E6F2;
  padding-top: 30px;
  border-radius: 10%;
  height: 200px;
  width: 350px;
  /* display: flex;
  flex-direction: row;
  align-items: center; */
}

.VictoryContainer {
  margin-top: 25px;
}

/* Footer */
.Footer {
  width: 100%;
  background-color: #E3E6F2;
  position: absolute; 
  left: 0 ; right: 0; bottom: 0; 
  height: 60px;
  margin-top: 15px;
  text-align: right;
}


